@import url(https://fonts.googleapis.com/css2?family=Red+Hat+Text:ital,wght@0,400;0,500;0,700;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root{
  --primary-blue: #25c5db;
  --primary-off-white: #e4f0f1;
  --primary-red: #640f05;
}

.App{
  background: #25c5db;
  background: var(--primary-blue);
  min-height: 100vh;
  overflow-x: hidden;
}

.boxwidth {
  width: 80%;
  min-width: 90vw;
  margin: 0 auto;
}

.clickToExit{
  position: absolute;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
}

.logo{
  -webkit-touch-callout:none;
  -webkit-user-select:none;
  -ms-user-select:none;
  user-select:none;
}
.Navigation{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: var(--primary-blue);
  border-radius: 0 0 20px 20px;
}

.fixed_Nav_home{
  z-index: 1;
  -webkit-animation: Navdrop 500ms ease-in;
          animation: Navdrop 500ms ease-in;
}

@-webkit-keyframes Navdrop{
  from{
    -webkit-transform: translateY(-10em);
            transform: translateY(-10em);
  } to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes Navdrop{
  from{
    -webkit-transform: translateY(-10em);
            transform: translateY(-10em);
  } to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

.Navigation nav{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  height: 5em;
}

.Navigation .nav_home{
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

.Navigation .logo_home{
  position: absolute;
  top: 10em;
  left: 0;
  width: 100%;
  overflow: hidden;
}

.Navigation .logo{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: baseline;
          align-items: baseline;
  font-family: "Red Hat Text", sans-serif;
  color: #fff;
}

.Navigation .logo h1 {
  font-style: italic;
  font-weight: bold;
  text-transform: uppercase;
}

.Navigation .logo_home h1{
  font-size: 18vw;
  -webkit-transform: translateX(-4vw);
          transform: translateX(-4vw);
}

.Navigation .logo h3{
  font-size: 1.2em;
  margin-left: 0.5em;
}

.Navigation .right .hamburger{
  cursor: pointer;
}

.Navigation .right .hamburger_menu {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  list-style: none;
  text-decoration: none;
  position: absolute;
  top: 4.3em;
  right: 5%;
  padding: 1em 0.5em;
  /* background: #fff; */
  border-radius: 5px;
  -webkit-transform: translateX(0);
          transform: translateX(0);
  transition: -webkit-transform 500ms ease;
  transition: transform 500ms ease;
  transition: transform 500ms ease, -webkit-transform 500ms ease;
  
  -webkit-touch-callout:none;
  -webkit-user-select:none;
  -ms-user-select:none;
  user-select:none;
}

.Navigation .right .menu_open{
  -webkit-animation: fadeIn 500ms ease;
          animation: fadeIn 500ms ease;
}

.Navigation .right .menu_close{
  -webkit-transform: translateX(15em);
          transform: translateX(15em);
  -webkit-animation: fadeOut 500ms ease;
          animation: fadeOut 500ms ease;
}

@-webkit-keyframes fadeIn{
  0% {
    display: -webkit-flex;
    display: flex;
    -webkit-transform: translateX(10em);
            transform: translateX(10em);
    opacity: 0;
  } 100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    opacity: 1;
  }
}

@keyframes fadeIn{
  0% {
    display: -webkit-flex;
    display: flex;
    -webkit-transform: translateX(10em);
            transform: translateX(10em);
    opacity: 0;
  } 100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    opacity: 1;
  }
}

@-webkit-keyframes fadeOut{
  0% {
    /* transform: translateX(0); */
    opacity: 1;
  } 75% {
    /* transform: translateX(10em); */
    opacity: 0;
  } 100% {
    display: none;
  }
}

@keyframes fadeOut{
  0% {
    /* transform: translateX(0); */
    opacity: 1;
  } 75% {
    /* transform: translateX(10em); */
    opacity: 0;
  } 100% {
    display: none;
  }
}

.Navigation .right .hamburger_menu li{
  font-family: "Red Hat Text",sans-serif;
  font-style: italic;
  font-weight: 700;
  color: #fff;
  text-align: right;
  padding: 0.5em 0;
  text-transform: uppercase;
  border-radius: 20px 0 0 20px;
  cursor: pointer;
  transition: all 100ms ease-in;
}

.Navigation .right .hamburger_menu li:hover{
  background: rgba(204, 204, 204, 0.466);
  padding: 0.5em 1em;
}

.Navigation .right_scrolled .hamburger_menu li{
  color: var(--primary-blue);
}

.Navigation .right_scrolled .hamburger_menu li:hover{
  color: #fff;
  background: var(--primary-blue);
}



.Carousel{
    background: var(--primary-red);
    min-height: 100vh;
    color: #fff;
}

.Carousel .Navigation{
    background: var(--primary-red);
}

.Carousel .carousel_container{
    padding-top: 16vh;
}

.Carousel .carousel_container .scroll_buttons{
    background: #fff;
    position: absolute;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    border-radius: 50px;
    padding: 0.5em;
    top: 50vh;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    cursor: pointer;
}

.Carousel .carousel_container .previous{
    left: 4vw;
}

.Carousel .carousel_container .next{
    right: 4vw;
}

.Carousel .carousel_container .scroll_buttons svg {
    fill: var(--primary-red);
}

.Carousel .carousel_container .previous svg{
    -webkit-transform: rotateY(-180deg);
            transform: rotateY(-180deg);
}

.Carousel .scroller{
    display: -webkit-flex;
    display: flex;
    /* max-width: 90vw; */
    padding: 0 5vw 1em 5vw ;
    overflow-x: scroll;
    -webkit-flex: none;
            flex: none;
    -webkit-flex-flow: row nowrap;
            flex-flow: row nowrap;
    -ms-scroll-snap-type: x mandatory;
        scroll-snap-type: x mandatory;
	scroll-behavior: smooth;
}

.Carousel .scroller::-webkit-scrollbar{
    background: transparent;
    height: 5px;
}

.Carousel .scroller::-webkit-scrollbar-thumb{
    background: #fff;
    border-radius: 20px;
}

.Carousel .scroller .carousel_img{
    width: 30em;
    max-width: 90vw;
    height: 40em;
    max-height: 70vh;
    overflow: hidden;
    -webkit-flex: none;
            flex: none;
    margin-right: 1em;
    scroll-snap-align: center;
    border-radius: 20px;
}

.Carousel .scroller .carousel_img img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}


.Footer{
    padding-top: 5em;
    position: relative;
    overflow: hidden;
    font-family: "Red Hat Text", sans-serif;
    background: var(--primary-off-white);
    color: #fff;
    height: 30em;
}

.Footer .curve{
    background: var(--primary-blue);
}

.Footer .Foot_body{
    margin-top: 5em;
    margin-bottom: 5em;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
}

.Footer .Foot_head{
    margin-bottom: 5em;
}

.Footer .Foot_head h1{
    font-weight: 700;
    font-style: italic;
    font-size: 2em;
    text-transform: uppercase;
}

.Footer .Foot_head p{
    font-weight: 500;
    text-transform: lowercase;
}

.Footer .Footer_links p{
    font-weight: 500;
}

.Footer .Footer_links .Foot_linkul{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    margin-top: 0.5em;
}

.Footer .Footer_links .Foot_linkul .Foot_link{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.Footer .Footer_links .Foot_linkul .Foot_link h3{
    font-size: 1em;
}


.Footer .Footer_links .Foot_linkul .Foot_link svg{
    margin-right: 1.5em;
}

.Footer a{
    text-decoration: none;
    color: #fff;
}

.Foot_bottom{
    background:#008ea1;
    position: absolute;
    bottom: 0;
    width: 100%;
}

.Foot_bottom .boxwidth{
    width: 92%;
}

.Foot_bottom .Foot_bot_body{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    padding: 1.5em 0;
}

.Foot_bottom .Foot_bot_body .logo{
    font-weight: 700;
    font-style: italic;
    font-size: 1em;
    text-transform: uppercase;
}

.Foot_bottom .Foot_bot_right{
    display: -webkit-flex;
    display: flex;
    list-style: none;
}

.Foot_bottom .Foot_bot_right li{
    margin-left: 1em;   
}

@media all and (max-width: 768px){
    .Footer .Footer_links .Foot_linkul{
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-align-items: flex-start;
                align-items: flex-start;
    }
    .Footer .Footer_links .Foot_linkul .Foot_link{
        margin-top: 0.5em;
    }
}   
.landing{
    margin-top: 80vh;
    padding-top: 8em;
    position: relative;
    overflow: hidden;
    padding-bottom: 5em;
}

.curve{
    position: absolute;
    top: 0;
    left: -100%;
    background: var(--primary-off-white);
    width: 300%;
    height: 100%;
    border-radius: 50% 50% 0 0;
    /* border-radius: 0 0 0 0; */
    transition: border-radius 500ms ease;
}

.landing .projects{
    margin-top: 10em;
}

@media all and (max-width: 768px){

    .landing{
        margin-top: 50vh;
    }

    .curve{
        left: -200%;
        width: 500%;
    }
    .landing .projects{
        margin-top: 7em;
    }
}

.ProjectCard{
    /* display: flex; */
    position: relative;
    /* background: rgba(100, 148, 237, 0.575); */
    margin-bottom: 3em;
    border-radius: 20px;
    overflow: hidden;
    font-family: "Red Hat Text", sans-serif;
    height: 28em;
}

.ProjectCard.boxwidth{
    min-width: 70vw;
}

.ProjectCard .left{
    width: 50vw;
    position: absolute;
    top: 0;
    left: 0;
}

.ProjectCard .left img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.ProjectCard .left,
.ProjectCard .right{
    /* height: 50vh; */
    height: 28em;
}

.ProjectCard .right{
    width: 43vw;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    padding: 3em;
    background: #fff;
    border-radius: 20px;
    position: absolute;
    top: 0;
    right: 0;
}

.ProjectCard .right .head{
    font-size: 2em;
    font-weight: 700;
    text-transform: uppercase;
}

.ProjectCard .right .description{
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 1.3em;
    font-weight: 500;
}

.ProjectCard .visit{
    background: #fd4242;
    padding: 1em;
    width: 20em;
    max-width: 90%;
    text-align: center;
    border-radius: 10px;
    color: #fff;
    font-style: italic;
    text-transform: uppercase;
    position: absolute;
    bottom: 3em;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}

.ProjectCard .visit:hover{
    -webkit-filter: saturate(1.2);
            filter: saturate(1.2);
}

.ProjectCard .visit:hover p{
    scale: 1.05;
}

@media all and (max-width: 768px){
    .ProjectCard{
        /* height: 70vh; */
        height: 35em;
    }
    .ProjectCard .left{
        width: 100%;
    }
    .ProjectCard .right{
        width: 100%;
        /* height: 40vh; */
        height: 20em;
        top: unset;
        bottom: 0;
        padding: 2em 3em;
    }
    .ProjectCard .right .head{
        font-size: 1.5em;
    }
    .ProjectCard .right .description{
        font-size: 1em;
    }
    .ProjectCard .visit{
        bottom: 1em;
    }
}

.SearchBox{
    position: absolute;
    top: 10em;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    background: #C3C3C35E;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    border-radius: 5px;
}

.SearchBox .SearchButton{
    display: -webkit-flex;
    display: flex;
    border-radius: 5px;
    padding: 0.8em 1em;
    cursor: pointer;
}

.SearchBox .SearchButton:hover{
    background: #a1a1a134;
}

.SearchBox input{
    outline: none;
    border: none;
    padding: 0 1.5em;
    width: 30em;
    max-width: 60vw;
    background: transparent;
    font-family: "Red Hat Text", sans-serif;
    font-weight: 500;
    font-size: 1em;
}

@media all and (max-width: 768px){
    .SearchBox .SearchButton{
        padding: 0.5em 1em;
    }
}
